@use 'base/mixins' as m;
@use 'base/variables' as v;

.lugares {
    

    &__grid {
        display: grid;
        grid-template-columns: repeat(4, 30rem);
        column-gap: 4rem;
        
        @include m.scrollHorizontal;
        @include m.tablet { 
            @include m.grid(2, 2rem);
            overflow: hidden;
            
         }
         @include m.desktop {
            @include m.grid(4, 2rem);
         } 
    }

}

.lugar {


    &__imagen {
        border-radius: 2rem;
    }
    
    &__nombre {
        margin: 2rem;
        font-size: 1.8rem;
        font-weight: bold;
    }
}