@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-image: url(../img/header.jpg);
    background-size: cover;
    background-position: center;
    padding: 3rem 0 30rem 0;

    &__busqueda {
        margin-top: 5rem;
        text-align: center;
        @include m.tablet {
            margin-top: 30rem;
        } 
             
    }

    &__busqueda-label {
        font-size: 2rem;
        font-weight: 700;
    }
    &__busqueda-boton {
        @include m.boton(v.$blanco, v.$primario);
    }
}
